@import "./variables.sass";

.App {
  text-align: left;
  max-height: 100vh;
  height: 100vh;
  overflow: hidden;
  background-color: #FBFAF7; }

.app-content {
  max-height: calc(100vh - 50px);
  height: 100%; }


.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }
